import React from "react";
import {
  Typography,
  Box,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableRow,
  Button,
} from "@material-ui/core";
import { Download } from "@material-ui/icons";

const Prescriptions = [
  {
    id: "1",
    name: "Infección Urinaria",
    date: "25/04/2022",
    doctorname: "Elite Admin",
    priority: "Download",
    pbg: "primary.main",
    budget: "3.9",
  },
  {
    id: "2",
    name: "Cefalea",
    date: "10/05/2022",
    doctorname: "Andrew McDownland",
    priority: "Download",
    pbg: "primary.main",
    budget: "24.5",
  },
  {
    id: "3",
    name: "Receta 2",
    date: "15/07/2022",
    doctorname: "Christopher Jamil",
    priority: "Download",
    pbg: "primary.main",
    budget: "12.8",
  },
  {
    id: "4",
    name: "Receta 3",
    date: "25/09/2022",
    doctorname: "Christopher JamilL",
    priority: "Download",
    pbg: "primary.main",
    budget: "2.4",
  },
];

const ExTableRecetas = () => {
  return (
    <Table
      aria-label="simple table"
      sx={{
        mt: 3,
        whiteSpace: "nowrap",
      }}
    >
      <TableHead>
        <TableRow>
          <TableCell>
            <Typography color="textSecondary" variant="h6">
              Id
            </Typography>
          </TableCell>
          <TableCell>
            <Typography color="textSecondary" variant="h6">
              Name Prescriptions
            </Typography>
          </TableCell>
          <TableCell>
            <Typography color="textSecondary" variant="h6">
              Name Doctor
            </Typography>
          </TableCell>
          <TableCell>
            <Typography color="textSecondary" variant="h6">
              Budget
            </Typography>
          </TableCell>
          <TableCell align="center">
            <Typography color="textSecondary" variant="h6">
              Download
            </Typography>
          </TableCell>

        </TableRow>
      </TableHead>
      <TableBody>
        {Prescriptions.map((Prescriptions) => (
          <TableRow key={Prescriptions.name}>
            <TableCell>
              <Typography
                sx={{
                  fontSize: "15px",
                  fontWeight: "500",
                }}
              >
                {Prescriptions.id}
              </Typography>
            </TableCell>
            <TableCell>
              <Box
                sx={{
                  display: "flex",
                  alignItems: "center",
                }}
              >
                <Box>
                  <Typography
                    variant="h6"
                    sx={{
                      fontWeight: "600",
                    }}
                  >
                    {Prescriptions.name}
                  </Typography>
                  <Typography
                    color="textSecondary"
                    sx={{
                      fontSize: "13px",
                    }}
                  >
                    {Prescriptions.date}
                  </Typography>
                </Box>
              </Box>
            </TableCell>
            <TableCell>
              <Typography color="textSecondary" variant="h6">
                {Prescriptions.doctorname}
              </Typography>
            </TableCell>
            <TableCell>
              <Typography color="textSecondary" variant="h6">
                {Prescriptions.budget}
              </Typography>
            </TableCell>
            <TableCell align="center">
            <Button 
                startIcon={<Download/> } title="Descargar" variant="contained" 
                sx={{ 
                  backgroundColor: Prescriptions.pbg,
                  color:"#FFFFFF", 
                  }}
                size="small"
                >
                  {Prescriptions.priority}
              </Button>
            </TableCell>
           
          </TableRow>
        ))}
      </TableBody>
    </Table>
  );
};

export default ExTableRecetas;
